.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.page-content {
  padding-top: 45px;
}

body {
  background-image: url("./images/halftone-yellow.png");
  background-color: #ffeb3b;
}

.modal {
  top:50px;
}

.modal-content {
  width:400px;
  margin: 0 auto;
  background: #f9a825;
}

.modal-body {
  margin-top:10px;
  width:380px;
  margin-left: 10px;
  font-size: 18px;
  font-weight: bolder;
}

.modal-header {
  margin-top:10px;
  padding-bottom: 3px;
}


/* .modal-submit {
  width: 100%;
  font-size: 16px;
  font-weight: bolder;
} */

#google-img {
  padding-left:15px;
}

#logo {
  width: 225px;
  -webkit-transform:translateY(-10px);
      -ms-transform:translateY(-10px);
          transform:translateY(-10px);
  height:auto;
  margin-left: 65px;
}

.modal-header,
.modal-footer {
  height: 60px;
}



