.card {
  margin-bottom: 10px;
}

.shop-img-container {
  border: 3px solid green;
}

.shop-img {
  width:100%;
  height:100%;
}

.shop-details {
  border-bottom: 3px solid green;
  border-left: 3px solid green;
  border-right: 3px solid green;
  background-image: url("../../images/halftone-yellow.png"); 
}

#interested-button,
#interested-button:hover  {
  font-family: sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bolder;
  background:yellow;
  color:  darkslategray;
  border: 2px solid darkslategray;
  border-radius: 1rem;
  padding: 6px;
  margin-bottom: 5px;
}

p,
#desc{
  color: darkslategray;
  font-size: 14px;
  font-weight: bold;
  padding:0 10px;
  margin-bottom: 0px;
}

.card-title {
  color: darkslategray;
  font-size: 16px;
  font-weight: bolder;
  padding:0 10px;
  height: 50px;
  overflow: hidden;
}



